import React, { Component } from 'react';
import './calscreen.css';
import { connect } from 'react-redux';
import { store } from 'react-notifications-component';
import notification from '../alert.notification'
class CalScr extends Component {
    state = {  }
    render() {
        const val = this.props.val
        return (
            <div className="screen">
                {
                    val.length > 25 ? () => { store.addNotification({ ...notification , message:"please enter the numbers within the box" , type:"danger" } ) } :  val}
            </div>
         );
    }
}
const mapStateToProps = (state) => ({
    val:state.val.val
}) 

export default connect(mapStateToProps)(CalScr);

