import React, { Component } from 'react';
import './calbtn.css'
import { connect } from 'react-redux';
import {append , calculate , clear} from '../../redux/calculator/action'
class CalBtn extends Component {
    constructor () {
        super()

        this.numbers=[
                {
                    id: 1,
                    val: 1,
                },
                {
                    id: 2,
                    val: 2,
                },
                 {
                    id: 3,
                    val: 3,
            },
            {
                    id: 10,
                val: '+',
            },
                 
                {
                    id: 4,
                    val: 4,
                },
             {
                    id: 5,
                    val: 5,
                },
                {
                    id: 6,
                    val: 6,
            },
            {
                id: 11,
                val:'-'
                },
                 {
                    id: 7,
                    val: 7,
                },
                {
                    id: 8,
                    val: 8,
                },
                {
                    id: 9,
                    val: 9,
            },
            {
                id: 13,
                val:"/",
            },
            {
                id: 14,
                val: '*'
            },
                {
                    id: 0,
                    val: 0,
                }
        ]
    }
    render() { 
        return (
            <div className="buttons">
                {
                    this.numbers.map(number => (
                        <button className="number" key={number.id} onClick={()=>this.props.append(number.val)}>
                            <span>
                                {number.val}
                            </span>
                        </button>
                    ))
                }
                <button className="number-btn" onClick={() => this.props.clear()}>
                    CLEAR
                </button>
                <button className="number-btns" onClick={()=>this.props.calculate()}>
                    =
                </button>
            </div>
        );
    }
}

const mapDisptachToProps = (dispatch) => ({
    append: (val) => (dispatch(append(val))),
    calculate: () => (dispatch(calculate())),
    clear: () => (dispatch(clear()))
})

 
export default connect(null , mapDisptachToProps)(CalBtn);