import React, { Component } from 'react';
import CalBtn from '../cal_buttons/cal_btn.component'
import CatScr from '../cal_screen/cal_scr.component'
import './calculator.css'

class Calculator extends Component {
    render() { 
        return ( 
            <>
            <div className="calculator">    
                <h1>CALCULATOR</h1>
                    <CatScr />
                    <CalBtn/>
                </div>

            </>
         );
    }
}
 
export default Calculator;